// (runtime-only or standalone) has been set in webpack.base.conf with an alias
// Created By HamiXR.

import Vue from 'vue'
import VueRouter from 'vue-router'
import '../static/reset.css'
// 
import VueI18n from 'vue-i18n'
import App from './App'
import Home from './components/Home'
import Committee from './components/Committee'
import Competition from './components/Competition'
import Constitution from './components/Constitution'
import About from './components/About'
//
import LectureTopic from './components/public/lectureTopic'
//
import Smith from './bio/Smith'
import Cong from './bio/Cong'
import Cristiano from './bio/Cristiano'
import Ferrati from './bio/Ferrati'
import Tamara from './bio/Tamara'
import Asaf from './bio/Asaf'
import Andrea from './bio/Andrea'
import Ingrid from './bio/Ingrid'
import Ivy from './bio/Ivy'
import Mark from './bio/Mark'

Vue.config.productionTip = false;
Vue.use(VueRouter);
Vue.use(VueI18n);

const i18n = new VueI18n({
  locale: 'en',
  // this.$i18n.locale //locale
  messages: {
    'en': require('./language/en.json'),
    'zh': require('./language/zh.json'),
    'it': require('./language/it.json')
  }
});

//
const router = new VueRouter({
  mode: "history",//URL#
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else if (to.hash) {
      return {selector: to.hash}
    } else {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      path: "/",
      component: Home
      // children: [//
      //   {
      //     path: "homeA",
      //     component: HomeA,
      //   }
      // ]
    },
    {
      path: "/committee",
      component: Committee
    },
    {
      path: "/Competition",
      component: Competition
    },
    {
      path: "/constitution",
      component: Constitution
    },
    {
      path: "/about",
      component: About
    },
    // 
    {
      path: "/lectureTopic",
      component: LectureTopic
    },
    // 
    {
      path: "/bio/smith",
      component: Smith
    },
    {
      path: "/bio/cong",
      component: Cong
    },
    {
      path: "/bio/cristiano",
      component: Cristiano
    },
    {
      path:"/bio/ferrati",
      component:Ferrati
    },
    {
      path: "/bio/tamara",
      component: Tamara
    },
    {
      path: "/bio/asaf",
      component: Asaf
    },
    {
      path: "/bio/andrea",
      component: Andrea
    },
    {
      path: "/bio/ingrid",
      component: Ingrid
    },
    {
      path: "/bio/ivy",
      component: Ivy
    },
    {
      path:"/bio/mark",
      component:Mark
    }
  ]
});

new Vue({
  el: '#app',
  router,
  i18n,
  components: {App},
  template: '<App/>'
});



